
.homepage {
  background-image: url("../../assets/home/background-home-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100vh;
  position:absolute;
}

#heading {
  margin-left: -860px;
  text-transform: uppercase;
  font-weight: 400;
  color: #d0d6f9;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 30px;
  line-height: 19px;
  letter-spacing: 2.7px;
}

h1 {
  color: white;
  font-weight: 400;
  font-size: 150px;
  line-height: 100px;
  text-transform: uppercase;
  margin: 57px auto 49px;
  margin-left: -810px;
}
.home-text {
    margin-left: 10vw;
    margin-top: 264px;
}
.home-text p {
  font-size: 18px;
  line-height: 32px;
  margin: 17px;
  margin-left: -15px;
  display: flex;
  justify-content: inherit;
  text-align: justify;
  font-family: "Barlow", sans-serif;
  width: 45%;
}

.explore-btn {
  font-weight: 400;
   font-size: 32px;
  line-height: 37px;
  letter-spacing: 2px;
   color: #0b0d17;
 border-radius: 50%;
   padding: 80px 30px;
   border: 0;
 background-color: white;
 text-transform: uppercase;
 position: absolute;
left: 50%;
 transform: translate(-50%);
 margin: 30px 0 60px;
   cursor: pointer;
  
    padding: 100px 50px;
    position: absolute;
    left: 80vw;
     top: 50%;
  
}
.explore-btn::after {
  content: "";
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.25);
  position: absolute;
  display: block;
  top: -60px;
  left: -27px;
  width: 320px;
 
}
.explore-btn:hover::after {
  width: 350px;
   height: 350px;

   top: -57px;
    left: -53px;
}

  #heading{
    margin-left: -210px;
    position: absolute;
    top: 247px;
  
  }
  h1{
    margin-left: 260px;
    position: absolute;
    top: 272px;
}
  
  .explore-btn{ 
    cursor: pointer;
    left: 71vw;
    top: 65%;
  }
  .home-text p{
    position: absolute;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    margin-left: -248px;
    left: 73vw;
    top: 42%;
  }


